.bg{
      position: fixed;
      width: 100%;
      height: 100vh;
      background: linear-gradient(135deg, var(--blue-700), var(--blue-300));
      top: 0;
      left: 0;
      z-index: -1;
}

.circle{
      border-radius: 50%;
      position: absolute;
}

.circle:nth-child(1){
      width: 300px;
      height: 300px;
      background: var(--blue-100);
      top: -150px;
      left: -150px;
      animation: move_1 20s ease-in-out infinite;
}

.circle:nth-child(2){
      width: 500px;
      height: 500px;
      background: var(--blue-500);
      top: 40%;
      left: 20%;
      animation: move_4 10s ease-in-out infinite;
}

.circle:nth-child(3){
      width: 400px;
      height: 400px;
      background: var(--blue-400);
      top: -100px;
      right: -20%;
      animation: move_2 10s ease-in-out infinite;
}

.circle:nth-child(4){
      width: 400px;
      height: 400px;
      background: var(--blue-300);
      bottom: -100px;
      right: -20%;
      animation: move_3 25s ease-in-out infinite;
}

.circle:nth-child(5){
      width: 200px;
      height: 200px;
      background: var(--blue-700);
      top: 20%;
      left: 60%;
      animation: move_5 20s ease-in-out infinite;
}


@keyframes move_1{
      0%{
            transform: translate(0, 0);
      }
      25%{
            transform: translate(30px, 100px);
      }
      50%{
            transform: translate(-80px, 0);
      }
      75%{
            transform: translate(0, 40px);
      }
      100%{
            transform: translate(0, 0);
      }
}

@keyframes move_2{
      0%{
            transform: translate(0, 0);
      }
      25%{
            transform: translate(100px, 300px);
      }
      50%{
            transform: translate(-50px, 80px);
      }
      75%{
            transform: translate(0, 140px);
      }
      100%{
            transform: translate(0, 0);
      }
}

@keyframes move_3{
      0%{
            transform: translate(0, 0);
      }
      25%{
            transform: translate(-30px, -100px);
      }
      50%{
            transform: translate(80px, -100px);
      }
      75%{
            transform: translate(0, -40px);
      }
      100%{
            transform: translate(0, 0);
      }
}

@keyframes move_4{
      0%{
            transform: translate(0, 0);
      }
      25%{
            transform: translate(130px, 100px);
      }
      50%{
            transform: translate(80px, 0);
      }
      75%{
            transform: translate(100px, -40px);
      }
      100%{
            transform: translate(0, 0);
      }
}

@keyframes move_5{
      0%{
            transform: translate(0, 0);
      }
      25%{
            transform: translate(60px, -100px);
      }
      50%{
            transform: translate(80px, -40px);
      }
      75%{
            transform: translate(-40px, 0);
      }
      100%{
            transform: translate(0, 0);
      }
}