
.load_content{
      width: 100%;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      background: var(--theme);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 1000;
}

.load_inner h1{
      display: flex;
      color: var(--blue-800);
      text-transform: uppercase;
      font-size: 2rem;
      letter-spacing: .2rem;
}

.load_inner h1 span{
      font-size: 2rem;
      font-weight: 500;
      opacity: 0;
      animation: alpha 3s ease-in-out infinite;
}

@keyframes alpha{
      0%{
            opacity: 0;
      }
      15%{
            opacity: 1;
      }
      30%{
            opacity: 0;
      }
      100%{
            opacity: 0;
      }
}