.modal{
      width: 100%;
      height: 100%;
      background: var(--alpha);
      -webkit-backdrop-filter: blur(10px);
      -moz-backdrop-filter: blur(10px);
      backdrop-filter: blur(10px);

      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 100;

      opacity: 0;
      visibility: hidden;
}

.modal.show{
      animation: show_modal .2s ease-out forwards;
}

@keyframes show_modal{
      0%{
            opacity: 0;
            visibility: hidden;
      }
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.friend_card{
      width: 190px;
      height: 200px;
      background: none;
      position: relative;
      transform: scale(2.5);
      opacity: 0;
}

.friend_card.show{
      animation: show_card .2s .2s ease-out forwards;
}

@keyframes show_card{
      0%{
            transform: scale(2.5);
            opacity: 0;
      }
      100%{
            transform: scale(1);
            opacity: 1;
      }
}

.friend_card:before{
      width: 100%;
      height: 160px;
      background: #4292c6;
      left: 0;
      bottom: 0;
}

.card_avatar{
      position: relative;
      width: 80px;
      height: 80px;
      overflow: hidden;
      box-shadow: 10px 10px 20px rgba(0,0,0,.2);
      margin-left: calc(50% - 40px);
}

.card_data{
      width: 100%;
      height: 90px;
      padding: 1rem 2rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
}

.card_data h2{
      text-transform: uppercase;
      color: var(--light);
      font-size: 1.2rem;
}

.card_data p{
      text-align: center;
      color: var(--light);
}

.card_menu{
      width: 100%;
      height: 30px;
      display: flex;
      align-items: center;
      position: relative;
}

.card_menu a,
.card_menu button{
      width: 50%;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--light);
      text-transform: uppercase;
      border: none;
      outline: none;
      cursor: pointer;
}

.card_menu a{
      background: var(--theme);
}

.card_menu button{
      background: var(--blue-700);
}