.content{
      width: 588px;
      height: auto;
      min-height: 420px;
      background: var(--light);
      box-shadow: 0 0 80px rgba(0,0,0,.2);
      border-bottom: solid 1rem var(--blue-300);
      overflow: hidden;
      position: relative;
}

.content_page{
      width: 100%;
      height: 100%;
}

.page_header,
.header_banner{
      width: 100%;
      height: 160px;
      position: relative;
      overflow: hidden;
}

.header_banner{
      position: absolute;
      top:  0;
      left: 0;
      display: flex;
      justify-content: center;
}

.header_content{
      position:  relative;
      width: 100%;
      height: 100%;
}

.header_content__menu{
      position: absolute;
      top: 1.5rem;
      right: 1.5rem;
      display: flex;
      gap: 1.5rem;
}

.header_content__menu a *{
      color: var(--light);
      font-size: 1.6rem;
}

.header_content__menu a,
.header_location{
      display: flex;
      align-items: center;
      height: 2.6rem;
      gap: .5rem;
      color: var(--light);
}

.header_content__menu a:hover{
      text-decoration: underline;
}

.header_content__menu a span{
      text-transform: uppercase;
      font-size: 1.4rem;
      letter-spacing: .1rem;
}

.header_location{
      height: 3.6rem;
      padding: .5em 1rem;
      background: var(--alpha);
      position: absolute;
      bottom: 1.5rem;
      right: 1.5rem;
      border-radius: .3rem;
      align-items: flex-end;
}

.header_location *{
      font-size: 2.2rem;
}

.header_location span{
      text-transform: uppercase;
      font-size: 1.3rem;
      font-weight: 500;
}

.container{
      height: calc(100% - 160px);
      position: relative;
      padding: 0 2rem;
      background: url(https://i.postimg.cc/0NmqFw2T/bg.png) right bottom no-repeat;
      background-size: 270px; 
}

.wrapper_user{
      width: 100%;
      height: auto;
      margin-top: -75px;
      display: flex;
      padding-left: 2rem;
      gap: 2rem;
}

.user_avatar{
      width: 150px;
      height: 150px;
      background: var(--blue-300);
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0 0 0 5px var(--alpha);
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
}

.user_data{
      flex-grow: 1;
      height: auto;
      padding-bottom: 2rem;
}

.user_online{
      height: 2rem;
      margin-top: 4rem;
      position: relative;
}

.user_online span{
      font-size: 1.4rem;
      text-transform: uppercase;
      color: var(--light);
}

.dots{
      width: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
      position: absolute;
      background: var(--theme);
      left: -3rem;
      top: .1rem;
}

.dots.yellow{
      background: var(--available);
      box-shadow: 0 0 10px rgba(255, 168, 45, 0.472);
}

.dots.green{
      background: var(--online);
      box-shadow: 0 0 10px rgba(62, 255, 45, 0.472);
}

.dots.red{
      background: var(--offline);
      box-shadow: 0 0 10px rgba(255, 45, 45, 0.472);
}

.user_data_infor{
      display: flex;
      flex-direction: column;
      margin-top: 2rem;
}

.user_data_infor h1{
      color: var(--blue-700);
      font-size: 3rem;
}

.user_data_infor strong{
      font-size: 1.4rem;
      margin-top: .5rem;
      letter-spacing: .3rem;
      color: #aaa;
      font-weight: 700;
}

.user_data_infor span{
      color: #aaa;
      font-size: 1.2em;
}

.user_data_about{
      margin-top: 1.5rem;
}

.user_data_about p{
      font-size: 1.4rem;
      color: var(--blue-800);
}

.wrapper_friends{
      margin-top: 2rem;
}

.friend_title{
      width: 100px;
      height: 27px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      border: solid 1px var(--blue-300);
      border-radius: 13.5px
}

.friend_title h2{
      text-transform: uppercase;
      line-height: 26px;
      width: 72px;
      text-align: center;
      color: var(--blue-700);
      font-weight: 400;
      font-size: 1rem;
}

.friend_title span{
      width: 26px;
      height: 26px;
      color: var(--light);
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--blue-400);
      font-size: 1.2rem;
      border-radius: 13px;
}

.friends_content{
      width: 100%;
      height: 80px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      position: relative;
}

.friend_row{
      width: 50px;
      height: 50px;
      border-radius: 50%;
      overflow: hidden;
      cursor: pointer;
      box-shadow: 0 0 0 5px var(--theme);
      background: var(--blue-500);
      cursor: pointer;
      position: relative;
      transition: .2s;
}

.friend_row img{
      width: 50px;
      height: 50px;
}

.friend_row:hover{
      z-index: 10;
      box-shadow: 0 0 0 8px var(--blue-500);
}