@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@100;200;300;400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

:root{
  /*fonts */
  --ft-1: 'Barlow Condensed', sans-serif;
  --ft-2: 'Poppins', sans-seif;

  /* colors */
  --theme: #a9daf5;
  --light: #fff;
  --alpha: rgba(255,255,255,.3);
  --blue-800: #08306b;
  --blue-700: #08519c;
  --blue-600: #4292c6;
  --blue-500: #6baed6;
  --blue-400: #9ecae1;
  --blue-300: #abd7ef;
  --blue-200: #a9daf5;
  --blue-100: #d4ecfa;
  --green: #b5ffcb;
  --yellow: rgb(253, 204, 45);
  --red: #ff0000;

  --available: linear-gradient(var(--yellow), rgb(255, 131, 36));
  --online: linear-gradient(var(--green), rgb(50, 165, 12));
  --offline: linear-gradient(var(--red), rgb(186, 11, 11));

  /* globals */
  font-size: 62.5%;
  font-family: var(--ft-2);
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  min-height: 800px;
  overflow-x: hidden;
  background: var(--theme);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

img{
  width:  100%;
  object-fit: cover;
}

ul{
  list-style: none;
}